import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { LoadingController, ToastController } from '@ionic/angular';
import { APIURL } from 'src/app/api-url';
import { ModalController } from '@ionic/angular';
import { ModalOtpPage } from '../modal-otp/modal-otp.page';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.page.html',
  styleUrls: ['./signup.page.scss'],
})
export class SignupPage implements OnInit {

  signForm: FormGroup;
  fullName    :string;
  email       :string;
  phoneNumber :number;
  password    :string;

  constructor(private formBuilder: FormBuilder, private router: Router, public http: HttpClient, public toastController: ToastController, public loadingCtrl: LoadingController, private modalCtrl: ModalController) { }

  ngOnInit() {
    this.signForm = this.formBuilder.group({
      fullName: [null, [Validators.required]],
      email: [null, [Validators.required]],
      phoneNumber: [null, [Validators.required]],
      password: [null, [Validators.required]],
    });
  }
  
  async presentModal() {
    const modal = await this.modalCtrl.create({
      component: ModalOtpPage
    });
    await modal.present();
  }

  signUp() {
    if (this.signForm.valid) {
      this.fullName    = this.signForm.value.fullName;
      this.email       = this.signForm.value.email;
      this.phoneNumber = this.signForm.value.phoneNumber;
      this.password    = this.signForm.value.password;
      
      let postData = {
        "nama": this.fullName,
        "email": this.email,
        "password": this.password,
        "phone": this.phoneNumber
      }

      
      this.presentLoadingWithOptions();
      setTimeout(() => {
        this.http.post(APIURL.apiURL + 'register_v2', postData)
          .subscribe(data => {
            console.log(data);
            let status = data['status'];
            if(status == "Success"){
              this.toastController.create({
                message: data['message'],
                position: 'bottom',
                duration: 5000,
                buttons: [
                  {
                    side: 'end',
                    icon: 'information-circle'
                  }
                ]
              }).then((toast) => {
                toast.present();
              });
              this.presentModal();
            }
            else{
              this.toastController.create({
                message: data['error'],
                position: 'bottom',
                duration: 5000,
                buttons: [
                  {
                    side: 'end',
                    icon: 'warning'
                  }
                ]
              }).then((toast) => {
                toast.present();
              });
            }
          });
        
      }, 1500);
    } 
    else{
      
    }
  }

  signIn(){
    this.router.navigate(['/signin']);
  }
  async presentLoadingWithOptions() {
    const loading = await this.loadingCtrl.create({
      spinner: 'lines',
      duration: 1500,
      message: 'Please wait...',
      translucent: true,
      cssClass: 'custom-class custom-loading'
    });
    return await loading.present();
  }
}
